.aside-menu .menu-nav {
    padding: 0; 
    margin: 12px 0 0;
    list-style: none;
}

.aside-menu .menu-nav .menu-item {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    float: none;
    padding: 0;
    position: relative;
    transition: all 0.3s ease-in-out;
}

.aside-menu .menu-nav>.menu-item>.menu-link {
    border: none;
    background: #fff;
    border-left: 2px solid transparent;
    cursor: pointer;
    display: block;
    margin: 0;
    min-height: 40px;
    outline: none;
    padding: 8px 0px;
    position: relative;
    text-decoration: none;
    text-align: center;
    transition: all 0.3s ease-in-out;
    outline: none;
    position: relative;
}

.newBadge {
    position: absolute;
    background: #4969ED;
    top: 4px;
    right: 4px;
    color: #fff !important;
    padding: 4px;
    font-size: 9px;
    line-height: normal;
    font-weight: 700;
    border-radius: 3px;
}

.newBadge_addfeed {
    position: absolute;
    background: #4969ED;
    top: -12px;
    right: -16px;
    color: #fff !important;
    padding: 5px;
    font-size: 10px!important;
    font-weight: 700 !important;
    border-radius: 4px;
    border: 2px solid #f2f6f8;
    line-height: 7px !important;
    display: block;
}

.aside-menu .menu-nav>.menu-item>.menu-link:hover, 
.aside-menu .menu-nav>.menu-item>.menu-link:focus, 
.aside-menu .menu-nav>.menu-item>.menu-link.active {
    background-color: #F3F6F9;
    border-left: 2px solid #4e69ed;
}

.aside-menu .menu-nav>.menu-item>.menu-link:hover svg path, 
.aside-menu .menu-nav>.menu-item>.menu-link:focus svg path, 
.aside-menu .menu-nav>.menu-item>.menu-link.active svg path {
    fill: #4e69ed;
}


.aside-menu .menu-nav>.menu-item>.menu-link:hover span, 
.aside-menu .menu-nav>.menu-item>.menu-link:focus span, 
.aside-menu .menu-nav>.menu-item>.menu-link.active span {
    color: #4e69ed;
}


.aside-menu .menu-nav>.menu-item>.menu-link .menu-icon{
    color:#333;
    font-size: 1.3rem;
}

.aside-menu .menu-nav>.menu-item>.menu-link .menu-icon svg {
    /* height: 18px;
    width: 21px; */
    margin-left: 0;
    height: 16px;
    width: 19px;
}

.aside-menu .menu-nav>.menu-item svg path {
    transition: all 0.3s ease-in-out;
}

.aside-menu .menu-nav .menu-item>.menu-link .menu-text {
    color: #333;
    display: block;
    font-weight: 600;
    font-size: 11px;
    line-height: 14px;
    padding: 0;
    transition: all 0.3s ease-in;
}

.aside-minimize .aside {
    width: 76px;
}

.aside-menu .right_submenu {
    position: fixed;
    left: 70px;
    top: 0;
    background: #fff;
    width:185px;
    height: 100%;
    border-right: 1px solid #f1f0f0;
    height: 100%;
    transition: all 0.3s ease-in-out;
}

.aside-menu .menu-nav>.menu-item>.menu-link.active+.right_submenu{
    left:76px;
    z-index: 9;
}

.aside-fixed .aside, .aside-minimize .aside, 
.aside-minimize-hover .aside, .subheader {
    transition: left .2s ease, width .2s ease;
}

.aside-menu .menu-nav>.menu-item>.menu-link:hover .svg-icon svg path
