.aside-menu .menu-nav>.menu-item>.menu-link.active .svg-icon svg path {
    fill: #4E69ED;
}

@media (min-width: 992px){
    .aside-minimize .aside {
        width: 76px;
    }
}


@media (min-width: 1200px){
    .aside-fixed .aside {
        position: fixed;
        top: 0;
        bottom: 0;
        border-right: 1px solid #f1f0f0;
        left: 0;
        z-index: 98;
        transform: translateX(0);
        visibility: visible;
    }
}







