    padding: 30px 18px;
    border-radius: 8px;
    z-index: 101;
}

.gallery_ready_popover::before {
    background-color: rgb(255, 255, 255);
    border-right: 1px solid rgb(225, 227, 234);
    border-top: 1px solid rgb(225, 227, 234);
    border-top-right-radius: 4px;
    bottom: 10px;
    content: "";
    height: 15px;
    position: absolute;
    left: -9px;
    transform: rotate(-150deg) skew(-24deg, 0deg);
    width: 15px;
}

.gallery_ready_popover h6 {
    font-size: 16px;
    font-weight: 600;
    line-height: 20px;
    color: #202223;
    margin-bottom: 7px;
}

.gallery_ready_popover p {
    font-size: 14px;
    font-weight: 500;
    line-height: 1.4;
    margin-bottom: 20px;
    color: #6D7175;
}

.gallery_ready_popover_closeBtn {
    border: none;
    background: transparent;
    padding: 10px;
    position: absolute;
    right: 0;
    top: 0;
}

.gallery_ready_popover_closeBtn svg {
    width: 20px;
    height: 20px;
}

.gallery_ready_popover_closeBtn svg path {
    fill: #6D7175;
}

.gallery_ready_btn.btn.btn-outline {
    border: 1px solid #BABFC3;
    color: #4E69ED;
}

.gallery_ready_btn.btn.btn-outline:hover,
.gallery_ready_btn.btn.btn-outline:focus {
    border: 1px solid #4E69ED;
    color: #4E69ED;
}


@media (min-width:1200px){
    .brand-toggle {
        display: none;
    }
}

@media (min-width:992px){
    .aside-fixed.aside-minimize .right_submenu {
        display: none;
    }

    .aside-menu .menu-nav>.menu-item:hover .right_submenu {
        display: block;
    }

    
}

@media screen and (max-width:1599px) and (min-width:1248px){
