}

@media screen and (max-width:1199px){
    .aside-menu .menu-nav>.menu-item .right_submenu,
    .aside-menu .menu-nav>.menu-item>.menu-link.active+.right_submenu {
        left: -200px;
    }

    .aside-fixed.aside.aside-on .aside-menu .menu-nav>.menu-item>.menu-link.active+.right_submenu{
        left: 70px;
    }

    .aside-fixed .aside{
        border-right: 1px solid #f1f0f0;
    }

    .gallery_ready_popover{
        display: none;
    }
    
}




@media screen and (max-width:991px){
    .aside-menu .right_submenu{
        right:-290px;
        left: auto;
    }

    .aside-menu .right_submenu{
        transition: right .3s ease, width .3s ease;
    }

    .aside-fixed.aside.aside-on .right_submenu{
        transition: left .2s ease, width .2s ease;
    }

    .aside-fixed.aside{
        border-left: 1px solid #DBE1E6;
    }

    .aside-menu .menu-nav>.menu-item>.menu-link.active+.right_submenu {
        left: -290px;
    }

}

